import React from 'react';
import PropTypes from 'prop-types';
import Icon24Chevron from '@vkontakte/icons/dist/24/chevron';

import twoWeekOptions from '../../utils/twoWeekOptions';
import styles from './DateSelect.module.css';

export default class DateSelect extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      title: ''
    };
  }

  static propTypes = {
    onChange: PropTypes.func,
    value: PropTypes.string
  };

  onChange = e => {
    this.setTitle();
    this.props.onChange && this.props.onChange(e);
  };

  setTitle = () => {
    const selectedOption = this.selectElement.options[
      this.selectElement.selectedIndex
    ];
    selectedOption &&
      this.setState({
        title: selectedOption.text
      });
  };

  componentDidMount() {
    this.setTitle();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.value !== this.props.value) {
      this.setTitle();
    }
  }

  getRef = element => {
    this.selectElement = element;
  };

  render() {
    const { value } = this.props;
    const { title } = this.state;
    return (
      <div className={styles.DateSelect}>
        <div>
          <div className={styles.Header}>Дата записи на прием</div>
          <div className={styles.Title}>{title}</div>
        </div>
        <div className={styles.Icon}>
          <Icon24Chevron />
        </div>
        <select onChange={this.onChange} value={value} ref={this.getRef}>
          {twoWeekOptions()}
        </select>
      </div>
    );
  }
}
