import { connect } from 'react-redux';
import { change } from 'redux-form';
import { withRouter } from 'react-router';

import actions from '../../actions';
import { getSpecialities, getSearchParams } from '../../selectors';
import SpecialitySelector from '../../components/panels/SpecialitySelector';

const mapStateToProps = state => {
  const { city } = getSearchParams(state);
  const { list, isLoading, error } = getSpecialities(state);
  return {
    isLoading,
    error,
    city: city.Id,
    items: list.map(speciality => ({
      key: speciality.Id,
      text: speciality.Name,
      speciality
    }))
  };
};

const mapDispatchToProps = (dispatch, { history }) => ({
  load: city =>
    dispatch({
      type: actions.LOAD_SPECIALITIES,
      payload: { city }
    }),
  onSelect: item => {
    dispatch(change('search', 'speciality', item.speciality));
    history.goBack();
  }
});

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(SpecialitySelector)
);
