import React from 'react';
import moment from 'moment';

export default () => {
  const options = [];
  for (let i = 0; i < 14; i += 1) {
    const date = moment()
      .add(i, 'day')
      .format('YYYY-MM-DD');
    options.push(
      <option key={date} value={date}>
        {moment(date)
          .locale('ru')
          .calendar(null, {
            sameDay: '[Сегодня]',
            nextDay: '[Завтра]',
            nextWeek: 'D MMMM, dddd',
            sameElse: 'D MMMM, dddd'
          })}
      </option>
    );
  }
  return options;
};
