import React, { Component } from 'react';
import * as Sentry from '@sentry/browser';
import { Root, View } from '@vkontakte/vkui';
import connect from '@vkontakte/vkui-connect';

import Crash from './components/panels/Crash';

class ExceptionBoundary extends Component {
  static propTypes = Component.propTypes;

  constructor(props) {
    super(props);
    this.state = { error: null };
  }

  componentDidCatch(error, errorInfo) {
    this.setState({ error });
    connect.send('VKWebAppSetViewSettings', {
      status_bar_style: 'dark',
      action_bar_color: 'white'
    });
    Sentry.withScope(scope => {
      Object.keys(errorInfo).forEach(key => {
        scope.setExtra(key, errorInfo[key]);
      });
      Sentry.captureException(error);
    });
  }

  render() {
    const { error } = this.state;
    if (error) {
      return (
        <Root activeView="crash">
          <View id="crash" activePanel="crashPanel">
            <Crash id="crashPanel" />
          </View>
        </Root>
      );
    }
    const { children } = this.props;
    return children;
  }
}

export default ExceptionBoundary;
