import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import { submit } from 'redux-form';
import moment from 'moment';

import { getOrder, getOrderForm, getConfirmationForm } from '../../selectors';
import Order from '../../components/panels/Order';
import actions from '../../actions';

const mapStateToProps = (state, { history }) => {
  const order = getOrder(state);
  const { doctor, slot, date, clinicId } = order;
  const clinic = doctor.Clinics.find(({ ClinicId }) => ClinicId === clinicId);
  const orderErrors = (getOrderForm(state) || {}).syncErrors || {};
  const confirmationErrors =
    (getConfirmationForm(state) || {}).syncErrors || {};
  return {
    goHome: () => history.push('/'),
    ...order,
    dateFormat: slot
      ? moment(slot.StartTime)
          .locale('ru')
          .format('D MMMM в H:mm')
      : moment(date)
          .locale('ru')
          .format('D MMMM'),
    cancel: () => history.goBack(),
    price:
      clinic.SpecialPrice ||
      clinic.Price ||
      (doctor.SpecialPrice || doctor.Price), // TODO: Выпилить костыль
    canPlacePreOrder: Object.keys(orderErrors).length === 0,
    canConfirm: Object.keys(confirmationErrors).length === 0
  };
};

const mapDispatchToProps = dispatch => ({
  onPreOrderClick: () => dispatch(submit('order')),
  onConfirmClick: () => dispatch(submit('confirmation')),
  dispatch
});

const mergeProps = (stateProps, dispatchProps, ownProps) => {
  const {
    doctor: { Id },
    clinicId,
    slot,
    date,
    phone,
    requestId
  } = stateProps;
  const { dispatch } = dispatchProps;
  return {
    ...stateProps,
    ...ownProps,
    ...dispatchProps,
    onPreOrderSubmit: params => {
      const { firstName, lastName, phone: phoneNumber } = params;
      dispatch({
        type: actions.PLACE_PRE_ORDER,
        payload: {
          name: [firstName, lastName].join(' '),
          phone: `7${phoneNumber}`,
          doctor: Id,
          clinic: clinicId,
          slotId: slot && slot.Id,
          dateAdmission: slot ? slot.StartTime : date,
          validate: 1
        }
      });
    },
    onConfirmSubmit: params => {
      dispatch({
        type: actions.CONFIRM_ORDER,
        payload: {
          ...params,
          requestId,
          phone
        }
      });
    }
  };
};

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps,
    mergeProps
  )(Order)
);
