import { takeLatest, put, call, select } from 'redux-saga/effects';
import axios from 'axios';

import actions from '../actions';
import denormalizeDoctors from '../utils/denormalizeDoctors';
import { getSearchParams } from '../selectors';

const composeDoctorUrl = id => `/doctor/${id}/withSlots/1/slotsDays/13`;
const composeReviewsUrl = id => `/review/doctor/${id}`;

export default function* loadDoctor({ payload: { id } }) {
  try {
    const searchParams = yield select(getSearchParams);
    const {
      data: { Doctor }
    } = yield call(axios, { url: composeDoctorUrl(id) });
    const doctor = denormalizeDoctors(Doctor, {
      speciality: searchParams.speciality.Id
    })[0];
    const {
      data: { ReviewList }
    } = yield call(axios, { url: composeReviewsUrl(id) });
    doctor.Reviews = ReviewList;
    yield put({
      type: actions.LOAD_DOCTOR_SUCCESS,
      payload: { doctor }
    });
  } catch (error) {
    yield put({
      type: actions.LOAD_DOCTOR_FAIL,
      payload: { error }
    });
  }
}

export function* watchLoadDoctor() {
  yield takeLatest(actions.LOAD_DOCTOR, loadDoctor);
}
