/* eslint-disable react/no-unused-state */ // Не учитывает условие
import React from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import { Button } from '@vkontakte/vkui';

import styles from './Slots.module.css';

const getNearestDate = (slots, date) => {
  return Object.keys(slots).find(k => k > date);
};

const formatDate = date =>
  moment(date)
    .locale('ru')
    .calendar(null, {
      sameDay: '[cегодня]',
      nextDay: '[завтра]',
      nextWeek: 'D MMMM',
      sameElse: 'D MMMM'
    });

const nearestDateDescription = (date, nearestDate) => {
  const result = [`К сожалению ${formatDate(date)} все занято`];
  if (nearestDate) {
    result.push(`ближайшая запись ${formatDate(nearestDate)}`);
  }
  return result.join(', ');
};

class Slots extends React.Component {
  static propTypes = {
    slots: PropTypes.objectOf(
      PropTypes.arrayOf(
        PropTypes.shape({
          Id: PropTypes.string.isRequired,
          StartTime: PropTypes.string.isRequired
        })
      )
    ).isRequired,
    onSelected: PropTypes.func,
    onDateChanged: PropTypes.func,
    date: PropTypes.string.isRequired
  };

  static defaultProps = {
    onSelected: () => {},
    onDateChanged: null
  };

  constructor(props) {
    super(props);
    this.state = {
      date: props.date
    };
  }

  onDateChanged(date) {
    this.setState({ date });
  }

  render() {
    const { slots, onSelected, onDateChanged } = this.props;
    const { date } = onDateChanged ? this.props : this.state;
    const now = moment();
    const slotsForDate = (slots[date] || []).filter(({ StartTime }) =>
      moment(StartTime).isAfter(now)
    );
    const nearestDate =
      slotsForDate.length > 0 ? null : getNearestDate(slots, date);
    return (
      <div>
        <div className={styles.Description}>
          {slotsForDate.length > 0 && `Расписание на ${formatDate(date)}`}
          {slotsForDate.length === 0 &&
            nearestDateDescription(date, nearestDate)}
        </div>
        <div className={styles.Slots}>
          {slotsForDate.length > 0
            ? slotsForDate.map(slot => {
                const { Id, StartTime } = slot;
                return (
                  <span className={styles.Slot} key={Id}>
                    <Button
                      level="commerce"
                      size="m"
                      onClick={e => {
                        e.stopPropagation();
                        onSelected(slot);
                      }}
                    >
                      {moment(StartTime).format('H:mm')}
                    </Button>
                  </span>
                );
              })
            : nearestDate && (
                <Button
                  level="outline"
                  size="m"
                  stretched
                  onClick={e => {
                    e.stopPropagation();
                    if (onDateChanged) {
                      onDateChanged(nearestDate);
                    } else {
                      this.onDateChanged(nearestDate);
                    }
                  }}
                >
                  {`Расписание на ${formatDate(nearestDate)}`}
                </Button>
              )}
        </div>
      </div>
    );
  }
}

export default Slots;
