import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { List, Cell, Search, Spinner, FixedLayout } from '@vkontakte/vkui';

import styles from './FilterableList.module.css';
import ErrorWrapper from '../moleculs/ErrorWrapper';

class FilterableList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      query: ''
    };
    this.onChange = this.onChange.bind(this);
  }

  onChange(query) {
    this.setState({ query });
  }

  render() {
    const {
      items,
      onSelect,
      isLoading,
      error,
      onRetry,
      renderItems
    } = this.props;
    const { query } = this.state;
    const filtered = items.filter(item =>
      item.text.toLowerCase().includes(query.toLowerCase())
    );
    return (
      <div className={styles.FilterableList}>
        <FixedLayout vertical="top">
          <Search theme="default" onChange={this.onChange} maxLength={100} />
        </FixedLayout>
        {isLoading ? (
          <div className={styles.Spinner}>
            <Spinner />
          </div>
        ) : (
          <ErrorWrapper error={error} onRetry={onRetry}>
            {filtered.length !== 0 ? (
              <List>{renderItems(filtered, onSelect)}</List>
            ) : (
              <div className={styles.NotFound}>Ничего не найдено</div>
            )}
          </ErrorWrapper>
        )}
      </div>
    );
  }
}

FilterableList.defaultProps = {
  ...ErrorWrapper.defaultProps,
  isLoading: false,
  renderItems: (items, onSelect) =>
    items.map(item => (
      <Cell key={item.key} onClick={() => onSelect(item)}>
        {item.text}
      </Cell>
    ))
};

FilterableList.propTypes = {
  ...ErrorWrapper.propTypes,
  items: PropTypes.arrayOf(
    PropTypes.shape({
      key: PropTypes.string,
      text: PropTypes.string
    })
  ).isRequired,
  isLoading: PropTypes.bool,
  onSelect: PropTypes.func.isRequired,
  renderItems: PropTypes.func
};

export default FilterableList;
