export const getCity = (state, id) =>
  state.entities.cities.list.find(city => city.Id === id);
export const getCities = state => state.entities.cities;
export const getStations = state => state.entities.stations;
export const getSpecialities = state => state.entities.specialities;
export const getSearchParams = state => state.form.search.values;
export const getSearch = state => state.entities.search;
export const getSelectedDoctor = state => state.entities.selectedDoctor;
export const getOrder = state => state.entities.order;
export const getOrderForm = state => state.form.order;
export const getConfirmationForm = state => state.form.confirmation;
