import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import { change } from 'redux-form';
import moment from 'moment';

import SearchResults from '../../components/panels/SearchResults';
import { getSearch, getSearchParams } from '../../selectors';
import actions from '../../actions';

const makeDesciption = ({ date, city, station }) => {
  const dateDescription = moment(date)
    .locale('ru')
    .calendar(null, {
      sameDay: '[cегодня]',
      nextDay: '[завтра]',
      nextWeek: 'D MMMM',
      sameElse: 'D MMMM'
    });
  const cityDescription = `в городе ${city.Name}`;
  const description = [dateDescription, cityDescription];
  if (station) {
    const stationDescription = `на станции ${station.Name}`;
    description.push(stationDescription);
  }
  return description.join(' ');
};

const mapStateToProps = (state, { history }) => {
  const search = getSearch(state);
  const query = getSearchParams(state);
  return {
    onBack: () => history.goBack(),
    search: {
      ...search,
      title: query.speciality.NamePlural,
      description: makeDesciption(query),
      hasMore: search.results.length < search.total
    },
    query
  };
};

const mapDispatchToProps = dispatch => ({ dispatch });

const mergeProps = (stateProps, dispatchProps, { history, ...ownProps }) => {
  const { dispatch } = dispatchProps;
  const {
    query,
    search: { page: currentPage }
  } = stateProps;
  const load = params =>
    dispatch({
      type: actions.SEARCH,
      payload: {
        city: query.city.Id,
        speciality: query.speciality.Id,
        when: query.date,
        subwayStation: query.station && query.station.Id,
        sort: query.sort,
        ...params
      }
    });
  return {
    ...stateProps,
    ...ownProps,
    loadMore: () => load({ page: currentPage + 1 }), // TODO: Переделать все на параметры из query-строки
    reload: () => load({ page: 0 }),
    onSortSelected: sort => {
      load({ sort, page: 0 });
      dispatch(change('search', 'sort', sort));
    },
    onDoctorSelected: doctor => {
      dispatch({
        type: actions.SELECT_DOCTOR,
        payload: { doctor }
      });
      history.push(`/doctor/${doctor.Id}`);
    },
    onOrder: params => {
      dispatch({
        type: actions.CREATE_ORDER,
        payload: params
      });
      history.push('/order');
    }
  };
};

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps,
    mergeProps
  )(SearchResults)
);
