import React from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router';
import { Panel, PanelHeader } from '@vkontakte/vkui';

import BackButton from '../atoms/BackButton';
import StationList from '../organisms/StationList';

class StationSelector extends React.Component {
  componentWillMount() {
    this.reload();
  }

  reload() {
    const { city, load } = this.props;
    load(city);
  }

  render() {
    const { id, isLoading, error, items, history, onSelect } = this.props;
    return (
      <Panel id={id}>
        <PanelHeader left={<BackButton onClick={() => history.goBack()} />}>
          Метро
        </PanelHeader>
        <StationList
          items={items}
          onSelect={onSelect}
          isLoading={isLoading}
          error={error}
          onRetry={() => this.reload()}
        />
      </Panel>
    );
  }
}

StationSelector.defaultProps = {
  error: null
};

StationSelector.propTypes = {
  id: PropTypes.string.isRequired,
  isLoading: PropTypes.bool.isRequired,
  error: PropTypes.oneOfType([PropTypes.string, PropTypes.shape]),
  city: PropTypes.string.isRequired,
  load: PropTypes.func.isRequired,
  items: PropTypes.arrayOf(PropTypes.object).isRequired,
  history: PropTypes.shape({
    onBack: PropTypes.func
  }).isRequired,
  onSelect: PropTypes.func.isRequired
};

export default withRouter(StationSelector);
