import React from 'react';
import PropTypes from 'prop-types';

import styles from './StationDistance.module.css';
import { ReactComponent as Icon13Pedestrian } from '../../svg/Icon13Pedestrian.svg';

const StationDistance = ({
  distance: {
    Station: { LineColor, Name },
    DistanceWalking
  }
}) => (
  <div className={styles.StationDistance}>
    <div className={styles.LinePin} style={{ background: `#${LineColor}` }} />
    <div>{Name}</div>
    <div>
      <Icon13Pedestrian />
    </div>
    <div>{`${DistanceWalking} м`}</div>
  </div>
);

StationDistance.propTypes = {
  distance: PropTypes.shape({
    Station: PropTypes.shape({
      LineColor: PropTypes.isRequired,
      Name: PropTypes.string.isRequired
    }),
    DistanceWalking: PropTypes.number.isRequired
  }).isRequired
};

export default StationDistance;
