import actions from '../actions';

export default (
  state = { isLoading: false, list: [], error: null },
  action
) => {
  switch (action.type) {
    case actions.LOAD_CITIES:
      return {
        ...state,
        error: null,
        isLoading: true
      };
    case actions.LOAD_CITIES_SUCCESS:
      return {
        isLoading: false,
        error: null,
        ...action.payload
      };
    case actions.LOAD_CITIES_FAIL:
      return {
        ...state,
        isLoading: false,
        ...action.payload
      };
    default:
      return state;
  }
};
