import React from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router';
import { Panel, PanelHeader } from '@vkontakte/vkui';

import BackButton from '../atoms/BackButton';
import FilterableList from '../organisms/FilterableList';

class SpecialitySelector extends React.Component {
  componentWillMount() {
    this.reload();
  }

  reload() {
    const { city, load } = this.props;
    load(city);
  }

  render() {
    const { id, isLoading, onSelect, items, history, error } = this.props;
    return (
      <Panel id={id} className="SpecialitySelector">
        <PanelHeader left={<BackButton onClick={() => history.goBack()} />}>
          Специалисты
        </PanelHeader>
        <FilterableList
          items={items}
          onSelect={onSelect}
          isLoading={isLoading}
          error={error}
          onRetry={() => this.reload()}
        />
      </Panel>
    );
  }
}

SpecialitySelector.defaultProps = {
  error: null
};

SpecialitySelector.propTypes = {
  id: PropTypes.string.isRequired,
  items: PropTypes.arrayOf(PropTypes.shape).isRequired,
  error: PropTypes.oneOfType([PropTypes.string, PropTypes.shape]),
  city: PropTypes.string.isRequired,
  isLoading: PropTypes.bool.isRequired,
  load: PropTypes.func.isRequired,
  history: PropTypes.shape({
    goBack: PropTypes.func
  }).isRequired,
  onSelect: PropTypes.func.isRequired
};

export default withRouter(SpecialitySelector);
