import React from 'react';
import { Panel, Link } from '@vkontakte/vkui';
import * as Sentry from '@sentry/browser';

import styles from './Crash.module.css';

const showReportDialog = () =>
  Sentry.showReportDialog({
    title: 'Расскажите о возникшей проблеме',
    subtitle: '',
    labelName: 'Ваше имя',
    labelEmail: 'Адрес электронной почты',
    labelComments: 'Описание проблемы',
    labelSubmit: 'Отправить',
    successMessage: 'Спасибо за обратную связь!'
  });

export default ({ id }) => (
  <Panel id={id}>
    <div className={styles.Crash}>
      <div className={styles.Icon}>🙀</div>
      <div className={styles.Title}>Похоже, приложение сломалось</div>
      <div className={styles.Message}>
        Попробуйте перезапустить (••• в правом верхнем углу, "Очистить кэш") или{' '}
        <Link onClick={showReportDialog} level="tertiary">
          расскажите нам о проблеме
        </Link>
      </div>
    </div>
  </Panel>
);
