import React from 'react';
import PropTypes from 'prop-types';
import { Input } from '@vkontakte/vkui';
import classnames from '@vkontakte/vkui/src/lib/classNames';
import getClassName from '@vkontakte/vkui/src/helpers/getClassName';
import Icon24Dropdown from '@vkontakte/icons/dist/24/dropdown';

import './SearchField.css';

const baseClassName = getClassName('Input');

const SearchField = ({
  onClick,
  alignment,
  status,
  getRef,
  className,
  getRootRef,
  icon,
  input: { value, onChange },
  placeholder,
  ...restProps
}) => (
  <div
    className="SearchField"
    onClick={onClick}
    role="menuitem"
    onKeyDown={e => e.key === 'Enter' && onClick()}
    tabIndex={-1}
  >
    <div
      className={classnames(
        baseClassName,
        {
          [`Input--${alignment}`]: alignment,
          [`Input--s-${status}`]: status
        },
        className
      )}
      ref={getRootRef}
      tabIndex="-1"
    >
      <div className="IconContainer IconLeft">{icon}</div>
      <input
        {...restProps}
        value={value || placeholder}
        onChange={onChange}
        className="Input__el"
        ref={getRef}
        type="button"
        style={{
          textAlign: 'left',
          overflow: 'hidden',
          color: value ? undefined : 'var(--content_placeholder_text)'
        }}
      />
      <div className="IconContainer IconRight">
        <Icon24Dropdown />
      </div>
      <div className="Input__border" />
    </div>
  </div>
);

SearchField.propTypes = {
  ...Input.propTypes,
  input: PropTypes.shape({
    onChange: PropTypes.func,
    value: PropTypes.any
  }).isRequired
};

export default SearchField;
