import { takeLatest, call, put, select } from 'redux-saga/effects';
import axios from 'axios';

import actions from '../actions';

const composeUrl = ({ city }) => `/metro/city/${city}`;
const getStations = state => state.entities.stations;

export function* loadStations({ payload }) {
  const { list, city } = yield select(getStations);
  if (city === payload.city && list.length > 0) {
    return;
  }
  try {
    const {
      data: { MetroList }
    } = yield call(axios, { url: composeUrl(payload) });
    yield put({
      type: actions.LOAD_STATIONS_SUCCESS,
      payload: {
        result: MetroList,
        city: payload.city
      }
    });
  } catch (error) {
    yield put({ type: actions.LOAD_STATIONS_FAIL, payload: { error } });
  }
}

export function* watchLoadStations() {
  yield takeLatest(actions.LOAD_STATIONS, loadStations);
}
