import React from 'react';
import PropTypes from 'prop-types';
import { reduxForm, Field, propTypes } from 'redux-form';
import { FormLayout, FormLayoutGroup, FormStatus } from '@vkontakte/vkui';
import SmsCodeField from '../atoms/SmsCodeField';

const validate = values => {
  const errors = {};
  if (!/\d{4}/.test(values.validationCode)) {
    errors.validationCode = 'Введите четырехзначный код';
  }
  return errors;
};

const ConfirmationForm = ({ disabled, handleSubmit, confirmationError }) => (
  <FormLayout onSubmit={handleSubmit}>
    <FormLayoutGroup top="Введите код из SMS">
      <Field
        name="validationCode"
        component={SmsCodeField}
        disabled={disabled}
        type="tel"
      />
    </FormLayoutGroup>
    {confirmationError && (
      <FormStatus state="error">{confirmationError}</FormStatus>
    )}
  </FormLayout>
);

ConfirmationForm.defaultProps = {
  disabled: false,
  confirmationError: null
};

ConfirmationForm.propTypes = {
  ...propTypes,
  disabled: PropTypes.bool,
  confirmationError: PropTypes.string
};

export default reduxForm({
  form: 'confirmation',
  validate
})(ConfirmationForm);
