import React from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router';
import { Panel, PanelHeader } from '@vkontakte/vkui';
import FilterableList from '../organisms/FilterableList';

import BackButton from '../atoms/BackButton';

class CitySelector extends React.Component {
  static propTypes = {
    id: PropTypes.string.isRequired,
    load: PropTypes.func.isRequired,
    items: PropTypes.arrayOf(PropTypes.object).isRequired,
    onSelect: PropTypes.func.isRequired,
    history: PropTypes.shape({
      goBack: PropTypes.func
    }).isRequired,
    isLoading: PropTypes.bool.isRequired,
    error: PropTypes.oneOfType([PropTypes.string, PropTypes.shape])
  };

  static defaultProps = {
    error: null
  };

  componentDidMount() {
    const { load, items, isLoading } = this.props;
    if (items.length === 0 && !isLoading) {
      load();
    }
  }

  render() {
    const { id, items, history, onSelect, isLoading, error, load } = this.props;
    return (
      <Panel id={id}>
        <PanelHeader left={<BackButton onClick={() => history.goBack()} />}>
          Выбор города
        </PanelHeader>
        <FilterableList
          items={items}
          onSelect={onSelect}
          isLoading={isLoading}
          error={error}
          onRetry={load}
        />
      </Panel>
    );
  }
}

export default withRouter(CitySelector);
