import actions from '../actions';

const defaultState = {
  doctor: null,
  date: null,
  slot: null,
  clinicId: null,
  isLoading: false,
  phone: null,
  needsConfirmation: false,
  requestId: null,
  success: false
};

export default (state = defaultState, action) => {
  switch (action.type) {
    case actions.CREATE_ORDER:
      return {
        ...defaultState,
        ...action.payload,
        slot: action.payload.slot || null
      };
    case actions.PLACE_PRE_ORDER:
    case actions.CONFIRM_ORDER:
      return {
        ...state,
        isLoading: true
      };
    case actions.PLACE_PRE_ORDER_FAIL:
      return {
        ...state,
        isLoading: false,
        preOrderError: action.payload.error
      };
    case actions.CONFIRM_ORDER_FAIL:
      return {
        ...state,
        isLoading: false,
        confirmationError: action.payload.error
      };
    case actions.PLACE_PRE_ORDER_SUCCESS:
      return {
        ...state,
        ...action.payload,
        isLoading: false,
        needsConfirmation: true,
        preOrderError: null
      };
    case actions.CONFIRM_ORDER_SUCCESS:
      return {
        ...state,
        isLoading: false,
        success: true
      };
    default:
      return state;
  }
};
