import { eventChannel } from 'redux-saga';
import { autofill } from 'redux-form';
import { take, call, put, all } from 'redux-saga/effects';
import connect from '@vkontakte/vkui-connect';

const createChannel = () => {
  return eventChannel(emit => {
    const eventHandler = e => emit(e);
    connect.subscribe(eventHandler);
    const unsubscribe = () => connect.unsubscribe(eventHandler);
    return unsubscribe;
  });
};

export default function* watchVkConnect() {
  const channel = yield call(createChannel);
  while (true) {
    try {
      const {
        detail: { type, data }
      } = yield take(channel);
      switch (type) {
        case 'VKWebAppGetUserInfoResult':
          yield all([
            put(autofill('order', 'firstName', data.first_name)),
            put(autofill('order', 'lastName', data.last_name))
          ]);
          break;
        case 'VKWebAppGetPhoneNumberResult':
          if (/7[\d]{10}/.test(data.phone_number)) {
            yield put(autofill('order', 'phone', data.phone_number.slice(-10)));
          }
          break;
        default:
          break;
      }
    } catch (error) {
      if (process.env.NODE_ENV === 'development') {
        // eslint-disable-next-line no-console
        console.log(error);
      }
    }
  }
}
