import { connect } from 'react-redux';
import { change, clearFields } from 'redux-form';
import { withRouter } from 'react-router';

import CitySelector from '../../components/panels/CitySelector';
import actions from '../../actions';
import { getCities } from '../../selectors';

const mapStateToProps = state => {
  const { list, isLoading, error } = getCities(state);
  return {
    items: list.map(city => ({
      key: city.Id,
      text: city.Name,
      city
    })),
    isLoading,
    error
  };
};

const mapDispatchToProps = (dispatch, { history }) => ({
  load: () => dispatch({ type: actions.LOAD_CITIES }),
  onSelect: item => {
    dispatch(change('search', 'city', item.city));
    dispatch(clearFields('search', false, false, ['station']));
    history.goBack();
  }
});

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(CitySelector)
);
