import React from 'react';
import PropTypes from 'prop-types';

import formatExperience from '../../utils/formatExperience';
import styles from './SpecialityAndExperience.module.css';

const SpecialityAndExperience = ({ experience, specialities }) => {
  const components = specialities.map(s => s.Name);
  components.push(`Стаж ${formatExperience(experience)}`);
  return (
    <div className={styles.SpecialityAndExperience}>
      {components.join(' • ')}
    </div>
  );
};

SpecialityAndExperience.propTypes = {
  specialities: PropTypes.arrayOf(
    PropTypes.shape({
      Name: PropTypes.string.isRequired
    })
  ).isRequired,
  experience: PropTypes.number.isRequired
};

export default SpecialityAndExperience;
