import React from 'react';
import PropTypes from 'prop-types';
import accounting from 'accounting';

import RatingStars from '../atoms/RatingStars';
import SpecialityAndExperience from '../atoms/SpecialityAndExperience';
import styles from './Doctor.module.css';
import Avatar from '../atoms/Avatar';

const formatPrice = price =>
  price && price !== 0
    ? accounting.formatMoney(price, {
        symbol: '₽',
        thousand: ' ',
        format: '%v %s',
        precision: 0
      })
    : 'Цена по запросу';

const Doctor = ({
  doctor: { Name, Price, SpecialPrice, Img, Rating, ExperienceYear }
}) => (
  <div className={styles.Doctor}>
    <div className={styles.Avatar}>
      <Avatar url={Img} />
    </div>
    <div className={styles.Info}>
      <div className={styles.Rating}>
        <RatingStars
          className={styles.Rating}
          value={parseFloat(`${Rating}`)}
        />
        <SpecialityAndExperience
          specialities={[]}
          experience={ExperienceYear}
        />
      </div>
      <div className={styles.Name}>{Name}</div>
      <div className={styles.Pricing}>
        <span className={styles.Price}>
          {formatPrice(
            SpecialPrice && SpecialPrice !== 0 ? SpecialPrice : Price
          )}
        </span>
        {SpecialPrice && SpecialPrice !== 0 && (
          <span className={styles.OldPrice}>{formatPrice(Price)}</span>
        )}
      </div>
    </div>
  </div>
);

Doctor.propTypes = {
  doctor: PropTypes.shape({
    Name: PropTypes.string.isRequired,
    Price: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    SpecialPrice: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    Img: PropTypes.string.isRequired,
    Rating: PropTypes.oneOfType([PropTypes.string, PropTypes.number])
      .isRequired,
    Specialities: PropTypes.arrayOf(
      PropTypes.shape({
        Name: PropTypes.string.isRequired
      })
    ).isRequired,
    ExperienceYear: PropTypes.number
  }).isRequired
};

export default Doctor;
