import React from 'react';
import PropTypes from 'prop-types';
import Icon28Place from '@vkontakte/icons/dist/28/place';

import styles from './Location.module.css';
import StationDistance from '../../atoms/StationDistance';

const Location = ({ street, house, stationsDistance }) => (
  <div className={styles.Location}>
    <div>
      <div className={styles.Address}>{`${street}, ${house}`}</div>
      <div className={styles.Stations}>
        {stationsDistance.map(distance => (
          <StationDistance key={distance.Station.Id} distance={distance} />
        ))}
      </div>
    </div>
    <div className={styles.Icon}>
      <Icon28Place />
    </div>
  </div>
);

Location.defaultProps = {
  stationsDistance: []
};

Location.propTypes = {
  street: PropTypes.string.isRequired,
  house: PropTypes.string.isRequired,
  stationsDistance: PropTypes.arrayOf(PropTypes.object)
};

export default Location;
