import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import vkConnect from '@vkontakte/vkui-connect';

import Home from '../../components/panels/Home';
import { getSearchParams } from '../../selectors';
import actions from '../../actions';

const resetStatusBar = () => {
  vkConnect.send('VKWebAppSetViewSettings', {
    status_bar_style: 'light'
  });
};

const mapStateToProps = (state, { history }) => {
  const { city } = getSearchParams(state);
  return {
    isInitialized: state.app.isInitialized,
    withSubway: [2, 3].indexOf(city.SearchType) !== -1,
    selectCity: () => {
      history.push('/cities');
      resetStatusBar();
    },
    selectSpeciality: () => {
      history.push('/specialities');
      resetStatusBar();
    },
    selectStation: () => {
      history.push('/stations');
      resetStatusBar();
    }
  };
};

const mapDispatchToProps = (dispatch, { history }) => ({
  onSearch: ({ sort, date, city, speciality, station }) => {
    dispatch({
      type: actions.SEARCH,
      payload: {
        city: city.Id,
        speciality: speciality.Id,
        when: date,
        subwayStation: station && station.Id,
        page: 0,
        sort
      }
    });
    history.push('/search');
    resetStatusBar();
  }
});

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(Home)
);
