import React from 'react';
import PropTypes from 'prop-types';
import { Tabs, HorizontalScroll, TabsItem } from '@vkontakte/vkui';

import './SortTabs.css';

class SortTabs extends React.Component {
  static propTypes = {
    onSelected: PropTypes.func,
    value: PropTypes.shape({
      by: PropTypes.string,
      ascending: PropTypes.bool
    })
  };

  static defaultProps = {
    onSelected: null,
    value: {
      by: 'popularity'
    }
  };

  constructor(props) {
    super(props);
    this.state = {
      selectedTab: props.value.by
    };
  }

  onSelectedTab(sort) {
    this.setState({
      selectedTab: sort.by
    });
    const { onSelected } = this.props;
    if (onSelected) {
      onSelected(sort);
    }
  }

  render() {
    const { selectedTab } = this.state;
    return (
      <Tabs theme="header" type="buttons">
        <HorizontalScroll>
          <TabsItem
            onClick={() => this.onSelectedTab({ by: 'popularity' })}
            selected={selectedTab === 'popularity'}
          >
            Популярные
          </TabsItem>
          <TabsItem
            onClick={() => this.onSelectedTab({ by: 'price', ascending: true })}
            selected={selectedTab === 'price'}
          >
            Доступные
          </TabsItem>
          <TabsItem
            onClick={() => this.onSelectedTab({ by: 'rating' })}
            selected={selectedTab === 'rating'}
          >
            Лучшие
          </TabsItem>
          <TabsItem
            onClick={() => this.onSelectedTab({ by: 'experience' })}
            selected={selectedTab === 'experience'}
          >
            Опытные
          </TabsItem>
        </HorizontalScroll>
      </Tabs>
    );
  }
}

export default SortTabs;
