import React from 'react';
import PropTypes from 'prop-types';

import RatingStars from '../../atoms/RatingStars';
import Photo from '../../atoms/Photo';
import styles from './Header.module.css';
import SpecialityAndExperience from '../../atoms/SpecialityAndExperience';

const Header = ({
  doctor: { Name, ImgFormat, Rating, Specialities, ExperienceYear }
}) => (
  <div>
    <Photo url={ImgFormat} />
    <div className={styles.Info}>
      <RatingStars value={parseFloat(`${Rating}`)} />
      <div className={styles.Name}>{Name}</div>
      <SpecialityAndExperience
        specialities={Specialities}
        experience={ExperienceYear}
      />
    </div>
  </div>
);

Header.propTypes = {
  doctor: PropTypes.shape({
    ImgFormat: PropTypes.string.isRequired,
    Rating: PropTypes.oneOfType([PropTypes.string, PropTypes.number])
      .isRequired,
    Name: PropTypes.string.isRequired,
    ExperienceYear: PropTypes.number.isRequired,
    Specialities: PropTypes.array.isRequired
  }).isRequired
};

export default Header;
