import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import { Root, View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import actions from './actions';
import match from './utils/match';
import Search from './containers/views/Search';
import Order from './containers/panels/Order';

const routes = [
  { id: 'order', params: { path: '/order' } },
  { id: 'search', params: { path: '/' } }
];

class App extends React.Component {
  static propTypes = {
    location: PropTypes.shape({
      pathname: PropTypes.string
    }).isRequired,
    loadCities: PropTypes.func.isRequired
  };

  componentDidMount() {
    const { loadCities } = this.props;
    loadCities();
  }

  render() {
    const {
      location: { pathname }
    } = this.props;
    const matched = match(routes, pathname);
    return (
      <Root activeView={matched ? matched.id : 'search'}>
        <Search id="search" />
        <View id="order" activePanel="orderPanel">
          <Order id="orderPanel" />
        </View>
      </Root>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  loadCities: () => dispatch({ type: actions.LOAD_CITIES })
});

export default withRouter(
  connect(
    null,
    mapDispatchToProps
  )(App)
);
