import React from 'react';
import PropTypes from 'prop-types';
import { Button } from '@vkontakte/vkui';

import styles from './ErrorWrapper.module.css';

const ErrorWrapper = ({ error, children, onRetry }) => (
  <div>
    {error ? (
      <div className={styles.ErrorWrapper}>
        <div className={styles.Title}>Что-то пошло не так...</div>
        <Button level="tertiary" size="m" onClick={onRetry}>
          Повторить попытку
        </Button>
      </div>
    ) : (
      <div>{children}</div>
    )}
  </div>
);

ErrorWrapper.defaultProps = {
  error: null,
  onRetry: null
};

ErrorWrapper.propTypes = {
  ...React.Component.propTypes,
  error: PropTypes.oneOfType([PropTypes.shape, PropTypes.string]),
  onRetry: PropTypes.func
};

export default ErrorWrapper;
