import { takeLatest, call, put } from 'redux-saga/effects';
import axios from 'axios';

import actions from '../actions';

export default function* loadCities() {
  try {
    const {
      data: { CityList }
    } = yield call(axios, { url: '/city' });
    yield put({
      type: actions.LOAD_CITIES_SUCCESS,
      payload: {
        list: CityList
      }
    });
  } catch (error) {
    yield put({
      type: actions.LOAD_CITIES_FAIL,
      payload: { error }
    });
  }
}

export function* watchLoadCities() {
  yield takeLatest(actions.LOAD_CITIES, loadCities);
}
