import actions from '../actions';

export default (
  state = { isLoading: false, list: [], error: null },
  { type, payload }
) => {
  switch (type) {
    case actions.LOAD_STATIONS:
      if (payload.city === state.city && state.list.length > 0) {
        return { ...state, isLoading: false };
      }
      return {
        isLoading: true,
        list: [],
        city: payload.city,
        error: null
      };
    case actions.LOAD_STATIONS_SUCCESS:
      return {
        ...state,
        isLoading: false,
        list: payload.result,
        error: null
      };
    case actions.LOAD_STATIONS_FAIL:
      return {
        ...state,
        isLoading: false,
        ...payload
      };
    default:
      return state;
  }
};
