import match from '../utils/match';

const routes = [
  { id: 'home', params: { path: '/', exact: true } },
  { id: 'search', params: { path: '/search' } },
  { id: 'cities', params: { path: '/cities' } },
  { id: 'stations', params: { path: '/stations' } },
  { id: 'specialities', params: { path: '/specialities' } },
  { id: 'doctor', params: { path: '/doctor/:id' } }
];

const route = (state, { location: { pathname }, action }) => {
  const { stack } = state;
  const matched = match(routes, pathname);
  if (!matched) {
    return stack;
  }
  if (matched.id === stack[stack.length - 1].id) {
    stack[stack.length - 1] = matched;
    return stack;
  }
  switch (action) {
    case 'POP':
      stack.pop();
      return stack.length === 0 ? [{ id: 'home', params: {} }] : stack;
    case 'PUSH':
      if (matched.id === 'home') {
        return [matched];
      }
      return [...stack, matched];
    default:
      return stack;
  }
};

export default (
  state = { stack: [{ id: 'home', params: {} }] },
  { type, payload }
) => {
  switch (type) {
    case '@app/ROUTE':
      return {
        ...state,
        stack: route(state, payload)
      };
    default:
      return state;
  }
};
