import { takeLatest, call, put } from 'redux-saga/effects';
import axios from 'axios';
import moment from 'moment';

import actions from '../actions';
import denormalizeDoctors from '../utils/denormalizeDoctors';

const limit = 20;

function composeUrl({
  city,
  speciality,
  when,
  sort,
  page,
  subwayStation,
  forChild,
  atHome
}) {
  const path = [];
  path.push(`start/${page * limit}/count/${limit}`);
  path.push(`city/${city}`);
  path.push(`speciality/${speciality}`);
  if (subwayStation !== undefined) {
    path.push(`stations/${subwayStation}`);
  }
  if (sort !== undefined) {
    path.push(`order/${sort.ascending ? '' : '-'}${sort.by}`);
  }
  path.push(`deti/${forChild ? 1 : 0}`);
  path.push(`na_dom/${atHome ? 1 : 0}`);
  path.push('withSlots/1/slotsDays/13');
  if (when) {
    path.push(`workingTime/${moment(when).format('YYYY-MM-DD')}`);
  }
  path.push('withoutAdultsReception/1');
  return `/doctor/list/${path.join('/')}`;
}

export function* search({ payload }) {
  try {
    const {
      data: { Total, DoctorList }
    } = yield call(axios, { url: composeUrl(payload) });
    yield put({
      type: actions.SEARCH_SUCCESS,
      payload: {
        page: payload.page,
        results: denormalizeDoctors(DoctorList, payload),
        total: parseInt(Total, 10)
      }
    });
  } catch (error) {
    yield put({
      type: actions.SEARCH_FAIL,
      payload: { error }
    });
  }
}

export function* watchSearch() {
  yield takeLatest(actions.SEARCH, search);
}
