import React from 'react';
import { Select } from '@vkontakte/vkui';
import classnames from '@vkontakte/vkui/src/lib/classNames';
import getClassName from '@vkontakte/vkui/src/helpers/getClassName';
import Icon24Dropdown from '@vkontakte/icons/dist/24/dropdown';
import { platform, ANDROID } from '@vkontakte/vkui/src/lib/platform';

import './SearchField.css';

const osname = platform();
const baseClassName = getClassName('Select');

export default class SearchSelect extends Select {
  render() {
    const {
      style,
      label,
      value,
      defaultValue,
      alignment,
      placeholder,
      children,
      className,
      getRef,
      getRootRef,
      icon,
      ...restProps
    } = this.props;

    return (
      <div className="SearchField">
        <label
          className={classnames(
            baseClassName,
            {
              [`Select--not-selected`]: this.state.notSelected,
              [`Select--align-${alignment}`]: alignment
            },
            className
          )}
          style={style}
          ref={getRootRef}
        >
          <div className="IconContainer IconLeft">{icon}</div>
          <select
            onChange={this.onChange}
            value={this.value}
            ref={this.getRef}
            {...restProps}
          >
            {placeholder && <option value="">{placeholder}</option>}
            {children}
          </select>
          <div className="Select__container">
            <div className="Select__title">{this.state.title}</div>
            <Icon24Dropdown />
          </div>
          {osname === ANDROID && <div className="Select__border" />}
        </label>
      </div>
    );
  }
}
