import { schema } from 'normalizr';

export const options = { idAttribute: 'Id' };
export const slot = new schema.Entity('Slots', {}, options);
export const station = new schema.Entity('Stations', {}, options);
export const speciality = new schema.Entity('Specialities', {}, options);
export const clinic = new schema.Entity(
  'ClinicsInfo',
  {
    Stations: [station]
  },
  { idAttribute: 'ClinicId' }
);
export const doctor = new schema.Entity(
  'Doctors',
  {
    ClinicsInfo: [clinic],
    Specialities: [speciality],
    Stations: [station],
    Slots: new schema.Values([slot])
  },
  options
);
export const search = new schema.Array(doctor);
