export default experience => {
  if (experience >= 11 && experience <= 14) {
    return `Найдено ${experience} врачей`;
  }
  if (experience % 10 === 1) {
    return `Найден ${experience} врач`;
  }
  if ([2, 3, 4].indexOf(experience % 10) !== -1) {
    return `Найдено ${experience} врача`;
  }
  return `Найдено ${experience} врачей`;
};
