import React from 'react';
import PropTypes from 'prop-types';
import { View, ConfigProvider } from '@vkontakte/vkui';
import connect from '@vkontakte/vkui-connect';

import Home from '../../containers/panels/Home';
import SearchResults from '../../containers/panels/SearchResults';
import CitySelector from '../../containers/panels/CitySelector';
import SpecialitySelector from '../../containers/panels/SpecialitySelector';
import StationSelector from '../../containers/panels/StationSelector';
import Doctor from '../../containers/panels/Doctor';

const onTransition = ({ to }) => {
  if (to === 'home') {
    connect.send('VKWebAppSetViewSettings', {
      status_bar_style: 'dark'
    });
  }
};

const Search = ({ activePanel, id, params, history, onSwipeBack }) => (
  <ConfigProvider isWebView>
    <View
      activePanel={activePanel}
      id={id}
      history={history}
      onSwipeBack={onSwipeBack}
      onTransition={onTransition}
    >
      <Home id="home" />
      <SearchResults id="search" />
      <CitySelector id="cities" />
      <SpecialitySelector id="specialities" />
      <StationSelector id="stations" />
      <Doctor id="doctor" params={params} />
    </View>
  </ConfigProvider>
);

Search.propTypes = {
  ...View.propTypes,
  params: PropTypes.shape({}).isRequired,
  history: PropTypes.arrayOf(PropTypes.string).isRequired,
  onSwipeBack: PropTypes.func.isRequired
};

export default Search;
