import React from 'react';
import PropTypes from 'prop-types';
import { FormLayout, FormLayoutGroup, FormStatus } from '@vkontakte/vkui';
import { reduxForm, propTypes, Field } from 'redux-form';
import connect from '@vkontakte/vkui-connect';

import TextField from '../atoms/TextField';
import PhoneField from '../atoms/PhoneField';

const validate = ({ firstName, lastName, phone }) => {
  const errors = {};
  if (!(firstName && firstName.trim() !== '')) {
    errors.firstName = 'Укажите имя';
  }
  if (!(lastName && lastName.trim() !== '')) {
    errors.lastName = 'Укажите фамилию';
  }
  if (!/(\d){10}/.test(phone)) {
    errors.phone = 'Укажите корректный телефонный номер';
  }
  return errors;
};

const parseName = value => {
  const match = value.match(/([а-яА-ЯЁёA-Za-z]+\s?)+/);
  if (!match) {
    return '';
  }
  return match[0];
};

class OrderForm extends React.Component {
  static propTypes = {
    ...propTypes,
    disabled: PropTypes.bool,
    orderError: PropTypes.string
  };

  static defaultProps = {
    disabled: false,
    orderError: null
  };

  componentDidMount() {
    connect.send('VKWebAppGetUserInfo', {});
    connect.send('VKWebAppGetPhoneNumber', {});
  }

  render() {
    const { handleSubmit, disabled, orderError } = this.props;
    return (
      <FormLayout onSubmit={handleSubmit}>
        {orderError && <FormStatus state="error">{orderError}</FormStatus>}
        <FormLayoutGroup top="Имя">
          <Field
            name="firstName"
            component={TextField}
            disabled={disabled}
            parse={parseName}
          />
        </FormLayoutGroup>
        <FormLayoutGroup top="Фамилия">
          <Field
            name="lastName"
            component={TextField}
            disabled={disabled}
            parse={parseName}
          />
        </FormLayoutGroup>
        <FormLayoutGroup top="Номер телефона">
          <Field
            name="phone"
            component={PhoneField}
            type="tel"
            disabled={disabled}
            parse={value => value.replace(/\D/g, '').slice(1)}
            defaultValue=""
          />
        </FormLayoutGroup>
      </FormLayout>
    );
  }
}

export default reduxForm({
  form: 'order',
  validate
})(OrderForm);
