export default experience => {
  if (experience >= 11 && experience <= 14) {
    return `${experience} лет`;
  }
  if (experience % 10 === 1) {
    return `${experience} год`;
  }
  if ([2, 3, 4].indexOf(experience % 10) !== -1) {
    return `${experience} года`;
  }
  return `${experience} лет`;
};
