import React from 'react';
import PropTypes from 'prop-types';
import { Button } from '@vkontakte/vkui';

import SlotButtons from '../Slots';

import styles from './Order.module.css';

const Order = ({
  slots,
  onSlotSelected,
  onOrderClick,
  date,
  onDateChanged
}) => (
  <div className={styles.Order}>
    {slots ? (
      <SlotButtons
        slots={slots}
        onSelected={onSlotSelected}
        date={date}
        onDateChanged={onDateChanged}
      />
    ) : (
      <div>
        <div className={styles.OrderButton}>
          <Button
            level="commerce"
            stretched
            onClick={e => {
              e.stopPropagation();
              onOrderClick();
            }}
          >
            Записаться
          </Button>
        </div>
        <div className={styles.OrderDescription}>
          В этой клинике у врача нет онлайн-расписания.
          <br />
          Оставьте заявку и мы перезвоним в течение 10 минут.
        </div>
      </div>
    )}
  </div>
);

Order.defaultProps = {
  slots: null,
  onOrderClick: () => {},
  onSlotSelected: () => {},
  onDateChanged: null
};

Order.propTypes = {
  slots: PropTypes.objectOf(PropTypes.array),
  onOrderClick: PropTypes.func,
  onSlotSelected: PropTypes.func,
  onDateChanged: PropTypes.func,
  date: PropTypes.string.isRequired
};

export default Order;
