import React from 'react';
import PropTypes from 'prop-types';

import styles from './DoctorCard.module.css';
import Doctor from '../moleculs/Doctor';
import Order from '../moleculs/Clinic/Order';
import Location from '../moleculs/Clinic/Location';

const DoctorCard = ({ doctor, date, onClick, onOrder }) => (
  <div
    className={styles.DoctorCard}
    onClick={onClick}
    onKeyUp={onClick}
    role="link"
    tabIndex="-1"
  >
    <div className={styles.Doctor}>
      <Doctor doctor={doctor} />
    </div>
    <div>
      {doctor.Clinics.map(
        ({ ClinicId, Street, House, StationsDistance, Slots }) => (
          <div key={ClinicId} className={styles.Clinic}>
            <Location
              house={House}
              street={Street}
              stationsDistance={StationsDistance}
            />
            <Order
              slots={Slots}
              date={date}
              onOrderClick={() => onOrder({ doctor, date, clinicId: ClinicId })}
              onSlotSelected={slot =>
                onOrder({ doctor, slot, clinicId: ClinicId })
              }
            />
          </div>
        )
      )}
    </div>
  </div>
);

DoctorCard.defaultProps = {
  date: undefined,
  onClick: undefined,
  onOrder: undefined
};

DoctorCard.propTypes = {
  date: PropTypes.string,
  doctor: PropTypes.shape({
    Clinics: PropTypes.arrayOf(
      PropTypes.shape({
        Id: PropTypes.string,
        Street: PropTypes.String,
        House: PropTypes.string,
        StationsDistance: PropTypes.arrayOf(PropTypes.object),
        Slots: PropTypes.objectOf(PropTypes.array)
      })
    ).isRequired
  }).isRequired,
  onClick: PropTypes.func,
  onOrder: PropTypes.func
};

export default DoctorCard;
