/* globals sessionStorage, window, document */
import React from 'react';
import PropTypes from 'prop-types';
import accounting from 'accounting';
import {
  Panel,
  PanelHeader,
  FixedLayout,
  Button,
  Spinner,
  Link,
  Tooltip,
  platform,
  IOS
} from '@vkontakte/vkui';

import styles from './Order.module.css';
import Avatar from '../atoms/Avatar';
import Cancel from '../atoms/Cancel';
import OrderForm from '../organisms/OrderForm';
import ConfirmationForm from '../organisms/ConfirmationForm';
import OrderSuccess from '../moleculs/OrderSuccess';

const os = platform();

const formatPrice = price =>
  price
    ? accounting.formatMoney(price, {
        symbol: '₽',
        thousand: ' ',
        format: '%v %s',
        precision: 0
      })
    : 'по запросу';

class Order extends React.Component {
  static propTypes = {
    id: PropTypes.string.isRequired,
    doctor: PropTypes.shape({
      Img: PropTypes.string
    }).isRequired,
    dateFormat: PropTypes.string.isRequired,
    cancel: PropTypes.func,
    price: PropTypes.number.isRequired,
    needsConfirmation: PropTypes.bool,
    isLoading: PropTypes.bool,
    success: PropTypes.bool,
    canPlacePreOrder: PropTypes.bool,
    canConfirm: PropTypes.bool,
    confirmationError: PropTypes.string,
    preOrderError: PropTypes.string,
    onPreOrderClick: PropTypes.func,
    onPreOrderSubmit: PropTypes.func,
    onConfirmClick: PropTypes.func,
    onConfirmSubmit: PropTypes.func,
    goHome: PropTypes.func
  };

  static defaultProps = {
    cancel: () => {},
    needsConfirmation: false,
    isLoading: false,
    success: false,
    canPlacePreOrder: false,
    canConfirm: false,
    confirmationError: null,
    preOrderError: null,
    onPreOrderClick: () => {},
    onPreOrderSubmit: () => {},
    onConfirmClick: () => {},
    onConfirmSubmit: () => {},
    goHome: undefined
  };

  constructor(props) {
    super(props);
    this.state = {
      needsShowTooltip: !sessionStorage.getItem('tooltipClosed')
    };
  }

  onTooltipClosed() {
    sessionStorage.setItem('tooltipClosed', true);
    this.setState({ needsShowTooltip: false });
  }

  renderButtonArea() {
    const {
      success,
      needsConfirmation,
      isLoading,
      onPreOrderClick,
      canPlacePreOrder,
      canConfirm,
      onConfirmClick,
      goHome
    } = this.props;
    // TODO: Избавиться от этого по возможности
    if (
      needsConfirmation &&
      window.innerHeight + window.scrollY < document.body.scrollHeight
    ) {
      window.scrollTo({
        top: document.body.scrollHeight - window.innerHeight,
        behavior: 'smooth'
      });
    }
    return (
      <div className={styles.OrderButtonArea}>
        {!success && !needsConfirmation && (
          <div>
            <div className={styles.Button}>
              <Button
                size="xl"
                level="commerce"
                onClick={onPreOrderClick}
                disabled={isLoading || !canPlacePreOrder}
              >
                {isLoading ? <Spinner /> : 'Записаться на приём'}
              </Button>
            </div>
            <div className={styles.Description}>
              Нажимая на кнопку “Записаться на приём”, я принимаю{' '}
              <Link href="https://vk.com/@doconlineapp-privacy">
                Пользовательское соглашение
              </Link>
            </div>
          </div>
        )}
        {!success && needsConfirmation && (
          <Button
            size="xl"
            level="commerce"
            disabled={isLoading || !canConfirm}
            onClick={onConfirmClick}
          >
            {isLoading ? <Spinner /> : 'Подтвердить номер телефона'}
          </Button>
        )}
        {success && (
          <Button size="xl" onClick={goHome}>
            Вернуться на главный экран
          </Button>
        )}
      </div>
    );
  }

  render() {
    const {
      id,
      cancel,
      dateFormat,
      price,
      doctor: { Img },
      needsConfirmation,
      isLoading,
      success,
      preOrderError,
      confirmationError,
      onPreOrderSubmit,
      onConfirmSubmit
    } = this.props;
    const { needsShowTooltip } = this.state;
    return (
      <Panel id={id}>
        <PanelHeader left={<Cancel onClick={cancel} />}>
          Запись на приём
        </PanelHeader>
        <div className={styles.Header}>
          <div className={styles.Avatar}>
            <Avatar url={Img} />
          </div>
          <div>
            <div className={styles.Info}>
              <div className={styles.Title}>Дата приёма</div>
              <div className={styles.Value}>{dateFormat}</div>
            </div>
            <div className={styles.Info}>
              <div className={styles.Title}>Цена</div>
              <Tooltip
                text="Приём оплачивается в клинике"
                isShown={needsShowTooltip}
                onClose={() => this.onTooltipClosed()}
              >
                <div className={styles.Value}>{formatPrice(price)}</div>
              </Tooltip>
            </div>
          </div>
        </div>
        {!success && (
          <div
            className={styles.Form}
            style={{ marginBottom: os === IOS ? '120px' : '0px' }}
          >
            <div className={styles.Title}>Личные данные</div>
            <OrderForm
              disabled={needsConfirmation || isLoading}
              onSubmit={onPreOrderSubmit}
              orderError={preOrderError}
            />
            {needsConfirmation && (
              <ConfirmationForm
                disabled={isLoading}
                onSubmit={onConfirmSubmit}
                confirmationError={confirmationError}
              />
            )}
          </div>
        )}
        {success && <OrderSuccess />}
        {os === IOS ? (
          <FixedLayout vertical="bottom">{this.renderButtonArea()}</FixedLayout>
        ) : (
          this.renderButtonArea()
        )}
      </Panel>
    );
  }
}

export default Order;
