import { takeLatest, call, put } from 'redux-saga/effects';
import axios from 'axios';

import actions from '../actions';

export default function* confirmOrder({ payload }) {
  try {
    const {
      data: {
        Response: { status, message }
      }
    } = yield call(axios, {
      method: 'post',
      url: '/request',
      data: payload
    });
    if (status !== 'success') {
      throw new Error(message);
    }
    yield put({ type: actions.CONFIRM_ORDER_SUCCESS });
  } catch (error) {
    yield put({
      type: actions.CONFIRM_ORDER_FAIL,
      payload: { error: error.message }
    });
  }
}

export function* watchConfirmOrder() {
  yield takeLatest(actions.CONFIRM_ORDER, confirmOrder);
}
