import React from 'react';
import PropTypes from 'prop-types';
import ReactHtmlParser from 'react-html-parser';
import uuid from 'uuid';

import ExperienceItem from '../moleculs/Experience';
import EducationItem from '../moleculs/Education';
import CourseItem from '../moleculs/Course';
import styles from './About.module.css';

const About = ({
  description,
  achievements: { Experience, Education, Courses }
}) => (
  <div className={styles.About}>
    <div className={styles.Header}>Специализация</div>
    <div className={styles.Description}>{ReactHtmlParser(description)}</div>
    {Experience.length > 0 && (
      <div>
        <div className={styles.Header}>Опыт работы</div>
        <div className={styles.List}>
          {Experience.map(e => (
            <ExperienceItem key={uuid()} experience={e} />
          ))}
        </div>
      </div>
    )}
    {Education.length > 0 && (
      <div>
        <div className={styles.Header}>Образование</div>
        <div className={styles.List}>
          {Education.map(e => (
            <EducationItem key={uuid()} education={e} />
          ))}
        </div>
      </div>
    )}
    {Courses.length > 0 && (
      <div>
        <div className={styles.Header}>Повышение квалификации</div>
        <div className={styles.List}>
          {Courses.map(e => (
            <CourseItem key={uuid()} course={e} />
          ))}
        </div>
      </div>
    )}
  </div>
);

About.propTypes = {
  achievements: PropTypes.shape({
    Experience: PropTypes.arrayOf(PropTypes.object),
    Education: PropTypes.arrayOf(PropTypes.object),
    Courses: PropTypes.arrayOf(PropTypes.object)
  }).isRequired,
  description: PropTypes.string.isRequired
};

export default About;
