import { normalize } from 'normalizr';
import moment from 'moment';
import { search as searchSchema } from '../schema';

const getStation = (entities, id) => entities.Stations[id];
const getSlot = (entities, id) => entities.Slots[id];
const groupSlots = slots =>
  slots.reduce((dateGroups, slot) => {
    const key = moment(slot.StartTime).format('YYYY-MM-DD');
    return {
      ...dateGroups,
      [key]: [...(dateGroups[key] || []), slot]
    };
  }, {});
const getClinic = (entities, id, query) => {
  const clinic = entities.ClinicsInfo[id];
  const selectedSpeciality = clinic.Specialities.find(
    s => `${s.SpecialityId}` === `${query.speciality}`
  );
  return {
    ...clinic,
    StationsDistance: clinic.StationsDistance.map(dist => ({
      ...dist,
      Station: getStation(entities, dist.Station)
    })),
    Price: selectedSpeciality && selectedSpeciality.Price,
    SpecialPrice: selectedSpeciality && selectedSpeciality.SpecialPrice
  };
};
const getSpeciality = (entities, id) => entities.Specialities[id];
const getDoctor = (entities, id, query) => {
  const doctor = entities.Doctors[id];
  return {
    ...doctor,
    Clinics: doctor.Clinics.map(clinicId => ({
      ...getClinic(entities, clinicId, query),
      Slots:
        doctor.Slots &&
        doctor.Slots[clinicId] &&
        groupSlots(
          doctor.Slots[clinicId].map(slotId => getSlot(entities, slotId))
        )
    })),
    Specialities: doctor.Specialities.map(specId =>
      getSpeciality(entities, specId)
    )
  };
};

export default (list, query) => {
  const { entities, result } = normalize(list, searchSchema);
  return result.map(id => getDoctor(entities, id, query));
};
