import React from 'react';
import PropTypes from 'prop-types';

import styles from './Experience.module.css';

const Experience = ({
  experience: { YearBegin, YearEnd, City, Position, Organization }
}) => {
  const years = [];
  if (YearBegin) {
    years.push(`${YearBegin} г.`);
  }
  if (YearEnd) {
    years.push(`${YearEnd} г.`);
  }
  const duration = years.join(' - ');
  const description = [];
  if (Position && Position !== '') {
    description.push(Position);
  }
  if (Organization && Organization !== '') {
    description.push(Organization);
  }
  return (
    <div className={styles.Experience}>
      <div className={styles.Header}>
        <div>{City}</div>
        <div className={styles.Years}>{duration}</div>
      </div>
      <div className={styles.Description}>{description.join(', ')}</div>
    </div>
  );
};

Experience.propTypes = {
  experience: PropTypes.shape({
    YearBegin: PropTypes.string,
    YearEnd: PropTypes.string,
    City: PropTypes.string,
    Position: PropTypes.string,
    Organization: PropTypes.string
  }).isRequired
};

export default Experience;
