import { connect } from 'react-redux';
import { withRouter } from 'react-router';

import actions from '../../actions';
import Doctor from '../../components/panels/Doctor';
import { getSelectedDoctor, getSearchParams } from '../../selectors';

const mapStateToProps = (state, { history }) => {
  const { doctor, isLoading, error } = getSelectedDoctor(state);
  const { date, speciality } = getSearchParams(state);
  return {
    doctor,
    achievements: doctor.Achievements,
    reviews: doctor.Reviews,
    isLoading,
    error,
    date,
    description: doctor.TextSpec,
    title: speciality.Name,
    back: () => history.goBack()
  };
};

const mapDispatchToProps = (dispatch, { history }) => ({
  load: id => {
    dispatch({
      type: actions.LOAD_DOCTOR,
      payload: { id }
    });
  },
  onOrder: params => {
    dispatch({
      type: actions.CREATE_ORDER,
      payload: params
    });
    history.push('/order');
  }
});

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(Doctor)
);
