import { put } from 'redux-saga/effects';
import { initialize } from 'redux-form';

import actions from '../actions';

const initialSearchParams = {
  city: {
    Name: 'Москва',
    Id: '1',
    SearchType: 2
  },
  speciality: {
    Name: 'Терапевт',
    NamePlural: 'Терапевты',
    Id: '91'
  },
  sort: {
    by: 'popularity'
  }
};

export default function* initApp() {
  yield put(initialize('search', initialSearchParams));
  yield put({ type: actions.APP_INITIALIZED });
}
