import actions from '../actions';

export default (
  state = { page: 0, results: [], isLoading: false, total: 0, error: null },
  action
) => {
  switch (action.type) {
    case actions.SEARCH:
      return {
        ...state,
        isLoading: true,
        error: null,
        results: action.payload.page === 0 ? [] : state.results
      };
    case actions.SEARCH_SUCCESS:
      return {
        ...state,
        isLoading: false,
        error: null,
        page: action.payload.page,
        total: action.payload.total,
        results:
          action.payload.page === 0
            ? action.payload.results
            : [...state.results, ...action.payload.results]
      };
    case actions.SEARCH_FAIL:
      return {
        ...state,
        isLoading: false,
        ...action.payload
      };
    default:
      return state;
  }
};
