/* globals window */
import React from 'react';
import PropTypes from 'prop-types';
import {
  Panel,
  PanelHeader,
  Tabs,
  TabsItem,
  Spinner,
  FixedLayout,
  Button
} from '@vkontakte/vkui';
import classnames from '@vkontakte/vkui/src/lib/classNames';
import moment from 'moment';

import BackButton from '../atoms/BackButton';
import Header from '../moleculs/DoctorDetail/Header';
import Clinic from '../organisms/Clinic';
import styles from './Doctor.module.css';
import About from '../organisms/About';
import DateSelect from '../moleculs/DateSelect';
import ErrorWrapper from '../moleculs/ErrorWrapper';
import ReviewList from '../organisms/ReviewList';

// TODO: Декомпозировать
class Doctor extends React.Component {
  constructor(props) {
    super(props);
    const { date } = this.props;
    this.state = {
      selectedTab: 'order',
      selectedDate: date,
      isJumpButtonShowing: false
    };
  }

  componentDidMount() {
    this.reload();
    window.addEventListener('scroll', this.onScroll, false);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.onScroll, false);
  }

  onScroll = () => {
    const { isJumpButtonShowing } = this.state;
    if (window.scrollY > window.innerHeight && !isJumpButtonShowing) {
      this.setState({ isJumpButtonShowing: true });
    } else if (window.scrollY < window.innerHeight && isJumpButtonShowing) {
      this.setState({ isJumpButtonShowing: false });
    }
  };

  onDateChanged(date) {
    this.setState({
      selectedDate: date
    });
  }

  reload() {
    const {
      load,
      doctor,
      achievements,
      reviews,
      description,
      params: { id }
    } = this.props;
    if (!doctor || !achievements || !reviews || !description) {
      load(id);
    }
  }

  selectTab(id) {
    this.setState({
      selectedTab: id
    });
  }

  render() {
    const {
      id,
      doctor,
      achievements,
      reviews,
      description,
      isLoading,
      error,
      clinicsLoading,
      title,
      back,
      onOrder
    } = this.props;
    const { selectedTab, selectedDate, isJumpButtonShowing } = this.state;
    return (
      <Panel id={id}>
        <PanelHeader
          left={<BackButton onClick={back} />}
          right={<div style={{ width: '88px ' }} />}
        >
          {title}
        </PanelHeader>
        <Header doctor={doctor} />
        {isLoading && (
          <div className={styles.Spinner}>
            <Spinner />
          </div>
        )}
        {!isLoading && (
          <ErrorWrapper error={error} onRetry={() => this.reload()}>
            <div className={styles.Tabs}>
              <Tabs style={{ background: 'white' }}>
                <TabsItem
                  onClick={() => this.selectTab('order')}
                  selected={selectedTab === 'order'}
                >
                  Записаться
                </TabsItem>
                <TabsItem
                  onClick={() => this.selectTab('reviews')}
                  selected={selectedTab === 'reviews'}
                >
                  {`Отзывы (${reviews.length})`}
                </TabsItem>
                <TabsItem
                  onClick={() => this.selectTab('about')}
                  selected={selectedTab === 'about'}
                >
                  О враче
                </TabsItem>
              </Tabs>
            </div>
            {selectedTab === 'order' && (
              <div>
                <div className={styles.DateSelect}>
                  <DateSelect
                    value={selectedDate}
                    onChange={e => this.onDateChanged(e.target.value)}
                  />
                </div>
                {clinicsLoading && (
                  <div className={styles.Spinner}>
                    <Spinner />
                  </div>
                )}
                {!clinicsLoading &&
                  doctor.Clinics.map(c => (
                    <div key={c.ClinicId} className={styles.Clinic}>
                      <Clinic
                        clinic={c}
                        date={selectedDate}
                        onDateChanged={date => this.onDateChanged(date)}
                        onOrder={params => onOrder({ ...params, doctor })}
                      />
                    </div>
                  ))}
              </div>
            )}
            {selectedTab === 'reviews' && (
              <div className={styles.ReviewList}>
                <ReviewList reviews={reviews} />
              </div>
            )}
            {selectedTab === 'about' && (
              <div className={styles.About}>
                <About description={description} achievements={achievements} />
              </div>
            )}
          </ErrorWrapper>
        )}
        {selectedTab !== 'order' && (
          <FixedLayout vertical="bottom">
            <div
              className={classnames(
                styles.GoToOrder,
                isJumpButtonShowing ? styles.SlideIn : styles.SlideOut
              )}
            >
              <Button
                size="xl"
                level="commerce"
                stretched
                onClick={() => this.selectTab('order')}
              >
                Записаться
              </Button>
            </div>
          </FixedLayout>
        )}
      </Panel>
    );
  }
}

Doctor.defaultProps = {
  description: '',
  reviews: [],
  achievements: {},
  date: moment().format('YYYY-MM-DD'),
  isLoading: true,
  error: null,
  clinicsLoading: false,
  title: null,
  params: {
    id: ''
  },
  load: () => {},
  onOrder: () => {}
};

Doctor.propTypes = {
  id: PropTypes.string.isRequired,
  load: PropTypes.func,
  onOrder: PropTypes.func,
  doctor: PropTypes.shape({
    Clinics: PropTypes.arrayOf(PropTypes.object)
  }).isRequired,
  achievements: PropTypes.shape(),
  reviews: PropTypes.arrayOf(PropTypes.object),
  description: PropTypes.string,
  date: PropTypes.string,
  isLoading: PropTypes.bool,
  error: PropTypes.oneOfType([PropTypes.string, PropTypes.shape]),
  clinicsLoading: PropTypes.bool,
  title: PropTypes.string,
  params: PropTypes.shape({
    id: PropTypes.string
  }),
  back: PropTypes.func.isRequired
};

export default Doctor;
