import actions from '../actions';

export default (
  state = { isLoading: false, list: [], error: null },
  action
) => {
  switch (action.type) {
    case actions.LOAD_SPECIALITIES:
      return {
        ...state,
        error: null,
        isLoading: true
      };
    case actions.LOAD_SPECIALITIES_SUCCESS:
      return {
        isLoading: false,
        error: null,
        list: action.payload.result
      };
    case actions.LOAD_SPECIALITIES_FAIL:
      return {
        ...state,
        isLoading: false,
        ...action.payload
      };
    default:
      return state;
  }
};
